import React from 'react';
import './App.css';
import '../node_modules/bootstrap/dist/css/bootstrap.min.css';
import '../node_modules/bootstrap/dist/js/bootstrap.bundle.min.js';
import  axios  from 'axios';
import { useState } from 'react';

import Navbar from './Navbar.js';
import Hero from './Hero.js';

function App() {
  const [data,setdata]=useState([])
  const getNews=()=>{
      axios.get("https://newsapi.org/v2/top-headlines?country=in&apiKey=e830f940165545bb9ddc720d7c47ebfb&q=india")
      .then((response)=>{
          console.log(response);
          setdata(response.data.articles)
      })
  }
  return (
    <>
        <Navbar/>
        <Hero/>
    <div className="container mt-2">
      <button className='btn btn-danger 'onClick={getNews}>More News</button>
    </div>

    <div className="contaiiner">
      <div className="row mt-4 g-0">

        {
          data.map((value)=>{
            return(
                <>
                <div className="col-12 col-md-6 col-lg-3  ">
                   <div className="card">
                    <img src={value.urlToImage} className="card-img-top " alt="..."/>
                   <div className="card-body">
                     <h5 className="card-title">{value.title}</h5>
                     <p className="card-text">{value.description}</p>
                    <a href={value.url} target='_blank' className="btn btn-primary">Main News</a>
                    </div>
                   </div>
                </div>
                </>
            );
          })
        }

      </div>
    </div>

    </>
  );
}

export default App;
